//import Navigation from '../NavBar/Navigation.js'
import ListGroup from 'react-bootstrap/ListGroup'
import React, { Component } from 'react'
import Badge from 'react-bootstrap/Badge'
import Navigation from './Navigation'


export default class Profile extends Component {
  
    
    
    render() {
        return (
            <div>
                
                <ListGroup as="ol" numbered>
  <ListGroup.Item
    as="li"
    className="d-flex justify-content-between align-items-start"
  >
    <div className="ms-2 me-auto">
      <div className="fw-bold">Nombre</div>
      AQUI SE METE EL NOMBRE QUE SALE DE LAS CREDENCIALES DE GOOGLE
    </div>
    <Badge variant="primary" pill>
      
    </Badge>
  </ListGroup.Item>
  <ListGroup.Item
    as="li"
    className="d-flex justify-content-between align-items-start"
  >
    <div className="ms-2 me-auto">
      <div className="fw-bold">Email</div>
      AQUI METE EN EMAIL DE LAS CREDENCIALES DE GOOGLE
    </div>
    <Badge variant="primary" pill>
      email de registro
    </Badge>
  </ListGroup.Item>
  <ListGroup.Item
    as="li"
    className="d-flex justify-content-between align-items-start"
  >
    <div className="ms-2 me-auto">
      <div className="fw-bold">Celular</div>
      AQUI VIENE EL NUMERO DE CELULAR QUE TIENE LA CUENTA DE GOOOGLE
    </div>
    <Badge variant="primary" pill>
      Celular
    </Badge>
  </ListGroup.Item>
</ListGroup>
            </div>
        )
    }
}
